.main-timeline2 {
    padding-top: 50px;
    overflow: hidden;
    position: relative;
    
}

.events{
    z-index: 20;
    position: relative;
}

.events h2{
    font-family: var(--pr_font);
}

hr{
    /* width: 2px; */
    margin: 0;
    color: rgb(119, 119, 119);

}

.main-timeline2:before {
    content: "";
    width: 7px;
    height: 100%;
    background: #084772;
    margin: 0 auto;
    position: absolute;
    top: 80px;
    left: 0;
    right: 0
}

.main-timeline2 .timeline {
    width: 50%;
    float: left;
    padding: 20px 60px;
    border-top: 7px solid #084772;
    border-right: 7px solid #084772;
    border-radius: 0 30px 0 0;
    position: relative;
    right: -3.5px;
    text-decoration: none;
    display: block;
}

.main-timeline2 .icon {
    display: block;
    width: 50px;
    height: 50px;
    line-height: 50px;
    border-radius: 50%;
    background: var(--color-pink);
    border: 1px solid #fff;
    text-align: center;
    font-size: 25px;
    color: #fff;
    box-shadow: 0 0 0 2px var(--color-pink);
    position: absolute;
    top: -30px;
    left: 0
}

.main-timeline2 .timeline-content {
    display: block;
    padding: 30px 10px 10px;
    border-radius: 20px;
    background: var(--color-pink);
    color: #fff;
    position: relative
}

.main-timeline2 .timeline-content:hover {
    text-decoration: none;
    color: #fff
}

.main-timeline2 .timeline-content:after,
.main-timeline2 .timeline-content:before {
    content: "";
    display: block;
    width: 10px;
    height: 50px;
    border-radius: 10px;
    background: var(--color-pink);
    border: 1px solid #fff;
    position: absolute;
    top: -35px;
    left: 50px
}


.main-timeline2 .timeline-content:after {
    left: auto;
    right: 50px
}

.main-timeline2 .title {
    font-size: 24px;
    margin: 0
}

.main-timeline2 .description {
    font-size: 15px;
    letter-spacing: 1px;
    margin: 0 0 5px
}

.main-timeline2 .timeline:nth-child(2n) {
    border-right: none;
    border-left: 7px solid #084772;
    border-radius: 30px 0 0;
    right: auto;
    left: -3.5px
}

.main-timeline2 .timeline:nth-child(2n) .icon {
    left: auto;
    right: 0;
    box-shadow: 0 0 0 2px #4bd9bf
}

.main-timeline2 .timeline:nth-child(2) {
    margin-top: 130px
}


.main-timeline2 .timeline:nth-child(odd) {
    margin: -130px 0 30px
}

.main-timeline2 .timeline:nth-child(even) {
    margin-bottom: 80px
}

.main-timeline2 .timeline:first-child,
.main-timeline2 .timeline:last-child:nth-child(even) {
    margin: 0 0 30px
}

.main-timeline2 .timeline:nth-child(2n) .icon,
.main-timeline2 .timeline:nth-child(2n) .timeline-content,
.main-timeline2 .timeline:nth-child(2n) .timeline-content:after,
.main-timeline2 .timeline:nth-child(2n) .timeline-content:before {
    background: #4bd9bf
}

.main-timeline2 .timeline:nth-child(3n) .icon,
.main-timeline2 .timeline:nth-child(3n) .timeline-content,
.main-timeline2 .timeline:nth-child(3n) .timeline-content:after,
.main-timeline2 .timeline:nth-child(3n) .timeline-content:before
{
    background: var(--color-orange)
}

.main-timeline2 .timeline:nth-child(3n) .icon {
    box-shadow: 0 0 0 2px var(--color-orange)
}

.main-timeline2 .timeline:nth-child(4n) .icon,
.main-timeline2 .timeline:nth-child(4n) .timeline-content,
.main-timeline2 .timeline:nth-child(4n) .timeline-content:after,
.main-timeline2 .timeline:nth-child(4n) .timeline-content:before,
.main-timeline2 .timeline:nth-child(7) .icon,
.main-timeline2 .timeline:nth-child(7) .timeline-content,
.main-timeline2 .timeline:nth-child(7) .timeline-content:after,
.main-timeline2 .timeline:nth-child(7) .timeline-content:before  {
    background: #3ebae7;
    
    box-shadow: 0 0 0 2px #3ebae7;
}

.main-timeline2 .timeline:nth-child(4n) .icon {
    box-shadow: 0 0 0 2px #3ebae7
}

@media only screen and (max-width:767px) {
    .main-timeline2:before {
        left: 0;
        right: auto
    }

    .main-timeline2 .timeline,
    .main-timeline2 .timeline:nth-child(even),
    .main-timeline2 .timeline:nth-child(odd) {
        width: 100%;
        float: none;
        padding: 20px 30px;
        margin: 0 0 30px;
        border-right: none;
        border-left: 7px solid #084772;
        border-radius: 30px 0 0;
        right: 0;
        
        left: 0
    }

    .main-timeline2 .icon {
        left: auto;
        right: 2px !important;
    }
}

@media only screen and (max-width:480px) {
    .main-timeline2 .title {
        font-size: 18px
    }
}